<template>
  <div>
    <div class="new-coins">
        <div class="mt30 new-coins-btn white pdtb20 pdlr20 ft12">
            <div>
                <p @click="msgDetail(167)" style="text-align: right;font-size: 15px;text-decoration: underline;" class="ptb5">{{$t('home.rule')}}</p>
            </div>
            <p class="ptb5 mt15" style="margin-bottom: 20px;font-size: 15px;">{{$t('home.rank')}}</p>
            <p class="flex alcenter tc ptb10 bdb">
				<span class="flex1">{{$t('home.rank_num')}}</span>
				<span class="flex1">{{$t('home.account')}}</span>
				<span class="flex1">{{$t('home.recharge')}}</span>
            </p>
			<div v-if="list.length==0" class="nomore">{{$t('td.nomore')}}</div>
            <div class="scroll list-content tc fColor1">
                <ul>
                    <li class="flex alcenter tc ptb10 bdb" v-for="(item,index) in list">
                        <span class="flex1">{{Number(index)+1}}</span>
                        <span class="flex1">{{item.account_number}}</span>
						<span class="flex1">{{item.value}}</span>
                    </li>
                </ul>
            </div>
        </div>
    </div>
  </div>
</template>

<script>
import "@/lib/clipboard.min.js"
// import indexHeader from "@/view/indexHeader";
// import indexFooter from "@/view/indexFooter";
export default {
    // components: { indexHeader, indexFooter },
    data() {
        return {
            content:'',
            code:'',
            extension_code:'',
            list:[],
            page:1,
            today_all_num:0,
            all_num:0,
            more:true
        };
    },
    created() {

    },
    mounted(){
        this.getDetail();
    },
    methods: {
		msgDetail(id) {
			var id = id;
			this.$router.push({
				name: 'noticeDetail',
				query: { id: id }
			});
			// let routeUrl = this.$router.resolve({
			//   name: "noticeDetail",
			//   query: { id: id }
			// });
			// window.open(routeUrl .href, '_blank');
		},
        getDetail(){
            this.$http({
                url: '/api/' + 'get_rechageLists',
                method:'get',
                data:{},
                headers: {'Authorization':  localStorage.getItem('token')},
            }).then(res=>{
				console.log(res)
                res = res.data;
                if(res.type  === 'ok'){
                    this.list=res.message;
                }else{
                    // layer.msg(res.message);
                }
            }).catch(error=>{
                console.log(error)
            })
        }
    }
};
</script>

<style scoped>
	.nomore{
		width: 100%;
		text-align: center;
		margin-top: 50px;
	}
.new-coins {
  min-width: 1200px;
  width: 100%;
  min-height: 600px;
}
.new-coins-header {
  width: 100%;
  height: 320px;
  background: url(../../static/imgs/app_banner3.jpg) no-repeat center;
}
.new-coins-header img {
  width: 100%;
  height: 320px;
}
.new-coins-btn {
  background-color: #22252b;
  width: 1200px;
  margin: 30px auto;
  border-radius: 4px;
}
.list-content{
    height: 400px;
    overflow-y: scroll;
}
.bor {
  border: 1px solid #aaa;
}
</style>



